.sidebar{
    position:fixed;
    grid-column: 1;
    width:20%;
    height:100vh;
    color:black;
    background-color: var(--prim-color);
    text-align: center;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.233);
    
}

.sidebar li{
    padding: 10px;
    font-size: 1.5rem;
    border-bottom: 1px groove #ffedb3;
    color:black;

}
.userName {
    margin-top:0;
    margin-bottom:20% ;
    padding-bottom: 10%;
    border-bottom: 8px groove #ffedb3;
}

.sidebar li:hover{
    transition: all 0.3s;
    background-color: #f17105;
}
.sidebar ul{
    list-style: none;
    padding: 0;
   
}

.sidebar .nav-link{
    text-decoration: none;
    color:black;
    
}
.sidebar .logout{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    color:white;
    background-color: rgb(190, 15, 15);
}
.sidebar .logout:hover{
    background:rgb(160, 10, 10)
}


